import { Actions, ActionTypes } from './user.actions';
import { ActionTypes as authActions } from '../../auth';
import { safeAction } from '../../shared/utils';


export interface State {
    _id: ObjectId;
    firstName: String;
    lastName: String;
    email: String;
    phone: String;
    gender: Gender;
    interests: Array<String>;
    birthday: Date;
    avatar: {
        mimeType: String,
        data: String
    };
    address: Address;
    friends: Array<User>;
    isProvider: Boolean;
};

const initialState: State = {
    _id: null,
    firstName: null,
    lastName: null,
    email: null,
    phone: null,
    gender: null,
    interests: [],
    birthday: null,
    avatar: {
        mimeType: null,
        data: null
    },
    address: null,
    friends: [],
    isProvider: false
};

function reducer(state = initialState, action: Actions): State {
    switch (action.type) {
        case ActionTypes.L_GET_USER: {
            return safeAction(action, state, (payload: User, newState) => {
                return Object.assign({}, newState, payload);
            });
        }

        case ActionTypes.L_USER_UPDATE: {
            return safeAction(action, state, (payload: User, newState) => {
                return Object.assign({}, newState, payload);
            });
        }

        default: {
            return state;
        }
    }
}

export const userReducer = { user: reducer };



