import { Actions, ActionTypes } from './provider.actions';

export interface State {
  _id: ObjectId;
  bio: String;
  rank: Number;
  fields: Array<String>;
  reviews: Array<Review>;
  businessAddress: Address;
  user: User;
  showContact: boolean;
};

const initialState: State = {
  _id: null,
  bio: '',
  rank: 0,
  fields: [],
  reviews: [],
  businessAddress: null,
  user: {firstName: '', lastName: '', avatar: {data: ''}},
  showContact: false
};

function reducer(state = initialState, action: Actions): State {
  switch (action.type) {

    case ActionTypes.TOGGLE_CONTACT: {
      return { ...state, showContact: !state.showContact };
    }

    case ActionTypes.L_GET_PROVIDER: {
      return { ...action.payload, showContact: state.showContact };
    }

    case ActionTypes.L_ADD_REVIEW: {
      return { ...state, reviews: [...state.reviews, action.payload] };
    }

    case ActionTypes.L_EDIT_REVIEW: {
      const tempArr = [...state.reviews];
      const index = tempArr.findIndex(review => review._id === action.payload._id);
      tempArr[index] = action.payload;

      return { ...state, reviews: tempArr };
    }

    case ActionTypes.L_DELETE_REVIEW: {
      const tempArr = [...state.reviews];
      const index = tempArr.findIndex(review => review._id === action.payload);
      tempArr.splice(index, 1);

      return { ...state, reviews: tempArr };
    }

    default: {
      return state;
    }
  }
}

export const providerReducer = { provider: reducer };



